<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <menu-con></menu-con>
    <mt-popup v-model="popupVisible" class="subpage" position="right">
      <mt-header title="二级页面" class="boder-header">
        <mt-button @click="hidePopup" icon="back" slot="left"></mt-button>
      </mt-header>
      <router-view name="subpage"></router-view>
    </mt-popup>
<!--     <mt-tabbar v-model="tabSelected" fixed>
      <mt-tab-item id="home">
        <img slot="icon" src="./assets/logo.png">首页
      </mt-tab-item>
      <mt-tab-item id="invite">
        <img slot="icon" src="./assets/logo.png">邀请
      </mt-tab-item>
      <mt-tab-item id="news">
        <img slot="icon" src="./assets/logo.png">资讯
      </mt-tab-item>
      <mt-tab-item id="mine">
        <img slot="icon" src="./assets/logo.png">我的
      </mt-tab-item>
    </mt-tabbar> -->
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import menuCon from '@/pages/index/menu'
export default {
  name: 'App',
  data () {
    return {
      tabSelected: 0,
      popupVisible: false
    }
  },
  components: {
    menuCon
  },
  methods: {
    changeStoreRoute (val) {
      switch (val.path) {
        case '/':
          this.tabSelected = 0
          break
        case '/hello':
          this.tabSelected = 1
          break
      }

      /** 显示、隐藏子页面 */
      if (val.meta.subpage) {
        this.popupVisible = true
      } else {
        this.popupVisible = false
      }
    },
    hidePopup () {
      showSubpage = false
    }
  },
  watch: {
    $route: 'changeStoreRoute',
    tabSelected: function (val, oldVal) {
      switch (val) {
        case 'home':
          this.$router.push('/')
          break
        case 'invite':
          this.$router.push('/mine/msgCenter')
          break
        case 'news':
          this.$router.push('/hot/fineNews')
          break
        case 'mine':
          this.$router.push('/mine')
          break
      }
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss">
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.subpage {
  width: 100%;
  height: 100%;
}

img {
  /* 图片兼容性处理 */
  content: normal !important;
  pointer-events: none;
}

html,
body {
  /* 去滚动条 */
  overflow: scroll;
  overflow-x: hidden;
  /* -webkit-overflow-scrolling: touch; */
  user-select: none;
  background: white;
}
body{
  padding-top: constant(safe-area-inset-top);
  padding-bottom: constant(safe-area-inset-bottom);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "dinm";
  src: url("/static/font/DINMittelschrift.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

a {
  text-decoration: none;
}

#app {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.bodyRel {
  position: relative;
}
.bodyFixed {
  position: fixed;
  width: 100%;
}

/*bankbanner*/
.bank-banner {
  background-color: none;
  & .swiper-container {
    & .swiper-pagination {
      & .swiper-pagination-bullet {
        width: 10px;
        height: 5px;
        background-color: #6b3ae3;
        border-radius: 1rem;
      }
    }
  }
}
.border-bottom {
  background-image: linear-gradient(
    180deg,
    #e6e6e6,
    #e6e6e6 50%,
    transparent 50%
  );
  background-repeat: no-repeat;
  background-size: 120% 1px;
  background-position: 0 100%;
}
.border-right {
  background-image: linear-gradient(
    90deg,
    #e6e6e6,
    #e6e6e6 50%,
    transparent 50%
  );
  background-repeat: no-repeat;
  background-size: 1px 100%;
  background-position: right top;
}
.single-image img {
  vertical-align: middle;
}
</style>



