<template>
    <div class="menu-wrap">
      <div v-for="(item,index) in option" :class="active==item.id?'active':''" size="small" @click.prevent="active = item.id;" @click="turnTo(item.url)">
        <div class="img">
          <img slot="icon" :src="active==item.id?item.src_f:item.src">
        </div>
        <div class="label">{{item.label}}</div>
        
      </div>
    </div>
  
</template>
<script>

export default {
  name: 'App',
  data() {
    return {
      tabSelected: 0,
      popupVisible: false,
      active: 'home',
      option: [{
          id: 'home',
          label: '首页',
          src_f: require('@/assets/images/menu_home_f.png'),
          src: require('@/assets/images/menu_home.png'),
          url:'/'

        },
        {
          id: 'invite',
          label: '邀请',
          src_f: require('@/assets/images/menu_invite_f.png'),
          src: require('@/assets/images/menu_invite.png'),
          url:'/mine/mineMsgList'
        },
        {
          id: 'news',
          label: '资讯',
          src_f: require('@/assets/images/menu_info_f.png'),
          src: require('@/assets/images/menu_info.png'),
          url:'/hot/fineNews'
        },
        {
          id: 'mine',
          label: '我的',
          src_f: require('@/assets/images/menu_my_f.png'),
          src: require('@/assets/images/menu_my.png'),
          url:'/mine'
        }
      ],
    }
  },
  components: {},
  methods: {
    turnTo(url){
      this.$router.push(url)
    },

  },
  watch: {
  },
  mounted() {}
}

</script>
<style lang='postcss' scoped> 

.menu-wrap{
  position:fixed;
  bottom:0;
  z-index:2;
  background:#fff;
  display:flex;
  height:50px;
  border-top: .5px solid #C6C6C6;

  &>div{
    margin: auto 20px; 

    & .img{
      display:flex;
      justify-content:center;
      align-items:center;
      & img{

        width:50%;
      }
    }
    & .label{
      display:flex;
      justify-content:center;
      align-items:center;
      font-size: 9px;
      color: #9EA8A9;
    }

  }
}

</style>



